.checkbox {
  margin-bottom: 0.2em;
  padding-left: 40px;
}

/*   input c type="checkbox" */
.custom-checkbox>input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

/*   label,   .custom-checkbox */
.custom-checkbox>span {
  font-family: PT Sans;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 31px;
  color: #FFFFFF;

  a {
    font-family: PT Sans;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 31px;
    color: #0a7bfd;
    text-decoration: underline;
    margin-left: 4px;
  }
}

/*   label  before    */
.custom-checkbox>span::before {
  content: '';
  display: inline-block;
  width: 25px;
  height: 25px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1.5px solid #fffefe;
  background-color: #fff;
  border-radius: 5px;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 70% 70%;
  position: absolute;
  left: 10px;
  cursor: pointer;
}




/*   ,    checked */
.custom-checkbox>input:checked+span::before {
  background-color: #fff;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 4.5l5 5L14.5 1' stroke='%23000' stroke-width='1.5'/%3E%3C/svg%3E");
}

/*   ,    disabled */
.custom-checkbox>input:disabled+span::before {
  background-color: #e9ecef;
}

.test-box__wrapp {
  .custom-radio>span::before {
    border-radius: 50%;
    background-size: 70%;
  }

  .custom-radio>input:checked+span::before {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%2301499B'/%3e%3c/svg%3e");
  }
}
