body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  font-family: "Roboto";
  color: #000000;
  background-color: #fff;
  overflow-x: hidden; }

html {
  box-sizing: border-box;
  overflow-x: hidden; }

*,
*::after,
*::before {
  box-sizing: inherit; }

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

a {
  display: inline-block;
  color: inherit;
  text-decoration: none;
  cursor: pointer; }

button {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0; }

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

input {
  width: 100%;
  outline: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.panel {
  z-index: 20;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out; }

.full-page {
  height: 100vh; }

.container {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 10px; }

.gradient-bg {
  background: linear-gradient(166.6deg, #FDDA27 45.67%, #59C3A5 104.41%, #2EBDC6 119.8%, rgba(255, 255, 255, 0) 119.81%);
  position: relative;
  z-index: 0; }

.mobile-burger {
  display: none; }

.mobile-mode button {
  display: none; }

.select-list::-webkit-scrollbar {
  width: 5px; }

.select-list::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.3); }

